import request from '@/utils/request'

export function getActionDetail(did) {//获取动态详情
  const data={
    did
  }
  return request({
    url:'/detail/dongtai',
    method:'post',
    data
  })
}

export function getActionList(uid,page) {//用户的动态列表
  const data={
    uid,
    page
  }
  return request({
    url:'/myspace/dtlist',
    method:'post',
    data
  })
}

export function actionList(plazatype,page) {//最新|最热 动态列表
  const data={
    plazatype,
    page
  }
  return request({
    url:'/dongtai/news',
    method:'post',
    data
  })
}
export function zpList(cpid,page) {//最新|最热 动态列表
  const data={
    cpid,
    page
  }
  return request({
    url:'/detail/showlist',
    method:'post',
    data
  })
}
export function  friendActionList(page) {//关注的最新动态
  const data={
    page
  }
  return request({
    url:'/dongtai/follownews',
    method:'post',
    data
  })
}

export function getShowList(cpid,page) {//获取动态列表
  const data={
    cpid,
    page
  }
  return request({
    url:'/detail/showlist',
    methods:'post',
    data
  })
}


export function pushDt(data) {//发布动态
  return request({
    url:'/publish/dongtai',
    method:'post',
    data
  })
}

export function delDt(dtid) {//删除动态
  const data={
    dtid
  }
  return request({
    url:'/action/deldt',
    method:'post',
    data
  })
}



