import request from '@/utils/request'

export function getMenuDetail(cpid) {//查看菜谱详情
  const data={
    cpid
  }
  return request({
    url:'/detail/recipe',
    method:'post',
    data
  })
}

export function pushMenu(data) {//发布菜谱
  return request({
    url:'/publish/recipe',
    method:'post',
    data
  })
}
export function getMenuEdit(cpid) {//获取编辑菜谱信息
  const data={
    cpid
  }
  return request({
    url:'/detail/editrecipe',
    method:'post',
    data
  })
}
export function pushMenuEdit(data) {//上传编辑菜谱
  return request({
    url:'/publish/editrecipesubmit',
    method:'post',
    data
  })
}
export function getMenuList(uid,page) {//菜谱列表
  const data={
    uid,
    page
  }
  return request({
    url:'/myspace/cplist',
    method:'post',
    data
  })
}
export function delCp(cpid) {//删除菜谱
  const data={
    cpid
  }
  return request({
    url:'/action/delcp',
    method:'post',
    data
  })
}




