<template>
  <div>
    <div class="bg-img cover xm" :style="{backgroundImage:'url('+ mainIcon +')'}"></div>
    <h1 class="mt50">Ta的食谱<span v-if="sortList.length>4"><router-link :to="{path: '/user', query: {id:uid}}" class="more" >更多<img src=""></router-link></span></h1>
    <ul class="like-list clearfix">
      <li class="item xs" v-for="(item,index) in sortList.slice(0,4)" :key="index" @click="bookDetail(item.id)">
        <a class="cover br5 bg-img xs" :style="{backgroundImage:'url('+ osshost + item.faceimg +')'}"
         >
        </a>
      </li>
    </ul>
<!--    <h1 class="mt50">被以下主题推荐</h1>-->
<!--    <ul class="theme-list  clearfix">-->
<!--      <theme-item :item="item" :size="1" v-for="(item,index) in themeList" :key="index"></theme-item>-->
<!--    </ul>-->
    <h1 class="mt50">热门搜索</h1>
    <div class="mt30">
      <div class="cu-tag bg-yellow mb10 br5 mr10" v-for="(item,index) in hotWordList" :key="index" @click="search(item)"> {{item}}</div>
    </div>
  </div>
</template>

<script>
  import ThemeItem from "./themeItem.vue";
  import FoodItem from "./foodItem.vue";
  import {getMenuList} from '@/api/menu'
  import {getHotWord} from '@/api/search'
  import {Message} from "element-ui";
    export default {
      name: "rightView",
      inject: ['reload'],
      components:{
        FoodItem,
        ThemeItem
      },
      props:['uid'],
      data(){
        return{
          osshost: process.env.UPLOAD_URL,
          listpic: '?x-oss-process=style/list-img',
          userface: '?x-oss-process=style/user-img',
          detailpic: '?x-oss-process=style/detail-img',
          mainIcon:"../../static/appqr.jpg",
          themeList: [{
            id: 1,
            iconImg: "https://cp1.douguo.com/upload/caiku/3/8/f/220x220_3826f2acc817f0b7b56d929320626c4f.jpeg",
            nickname: "夏日颜值担当|来一碗蛋黄炒饭吧",
            author: "吃肉不长胖的轧轧",
            authorImg: '../../static/logo.png',
            likeNum: 5625,
          }, {
            id: 2,
            iconImg: "https://cp1.douguo.com/upload/caiku/3/8/f/220x220_3826f2acc817f0b7b56d929320626c4f.jpeg",
            nickname: "夏日颜值担当|来一碗蛋黄炒饭吧",
            author: "吃肉不长胖的轧轧",
            authorImg: '../../static/logo.png',
            likeNum: 5625,
          }],
          sortList: [],
          hotWordList:[]
        }
      },
      created(){
        this.getBookList()
        this.getHotList()
      },
      methods:{
        getBookList(){
          getMenuList(this.uid,1).then(response => {
            let res = response.data;
            if (res.success) {
              this.sortList = res.result;
            } else {
              Message({
                message: response.message,
                type: 'error',
                duration: 5 * 1000
              })
            }
          }).catch(error => {
          })
        },
        getHotList(){
          getHotWord().then(response => {
            let res = response.data;
            if (res.success) {
              this.hotWordList = res.result;
            } else {
              Message({
                message: response.message,
                type: 'error',
                duration: 5 * 1000
              })
            }
          }).catch(error => {
            console.log(error)
          })
        },
        search(i){
          this.$router.push({path: "/search", query: {keyword:i}})
        },
        bookDetail(id) {
          this.$router.push({path: "/cookBook", query: {id: id}})
          this.reload()
        },
      }
    }
</script>

<style scoped>
.cover.xm {
    width: 280px;
    height: 390px;
}
</style>



