import request from '@/utils/request'

export function getHotWord() {
  return request({
    url:'/search/hotword',
    method:'post',
  })
}

export function searchMenuList(key,sort,page) {
  const data={
    key,
    sort,
    page
  }
  return request({
    url:'/search/recipe',
    method:'post',
    data
  })
}
export function searchUserList(name,page) {
  const data={
    name,
    page
  }
  return request({
    url:'/search/member',
    method:'post',
    data
  })
}



