import md from './md5'
//请求阿里云 oss
import Crypto from './upload/crypto.js';
import './upload/hmac.js';
import './upload/sha1.js';
import {
  Base64
} from './upload/base64.js';


//  oss 配置
var uploadFileSize = 1024 * 1024 * 100; // 上传文件的大小限制100m
var policyText = {
  "expiration": "2022-01-01T12:00:00.000Z", //设置该Policy的失效时间，超过这个失效时间之后，就没有办法通过这个policy上传文件了
  "conditions": [
    ["content-length-range", 0, uploadFileSize] // 设置上传文件的大小限制
  ]
};

function imgMd5(imgUrl, imgPath, pathurl) { //MD5加密 及拼接上传文件地址
  let str1 = md.hex_md5(imgUrl);
  let start = str1.substring(0, 1);
  let str2 = str1.substring(1);
  let mid = str2.substring(0, 1);
  let end = str1.substr(str1.length - 1, 1);
  imgPath = pathurl + '/' + start + '/' + mid + '/' + end + '/' + str1 + '.jpg';
  return imgPath;
}
// 文件上传
function fileUpload(osshost,imgPath, path, accessid, accesskey, accesstoken,callback) {
  let policyBase64 = Base64.encode(JSON.stringify(policyText));
  let message = policyBase64;
  let bytes = Crypto.HMAC(Crypto.SHA1, message, accesskey, {
    asBytes: true
  });
  let signature = Crypto.util.bytesToBase64(bytes);
  let self = this;
  return path;
}
export default {
  imgMd5,
  fileUpload
}



