import request from '@/utils/request'

export function getThemeDetail(topicid) {//获取主题详情
  const data={
    topicid
  }
  return request({
    url:'/subject/topicdetail',
    method:'post',
    data
  })
}

export function getSelfThemeList(page) {//获取用户主题列表
  const data={
    page
  }
  return request({
    url:'/mytopic/list',
    method:'post',
    data
  })
}
export function getUserThemeList(userid,page) {//获取好友主题列表
  const data={
    userid,
    page
  }
  return request({
    url:'/mytopic/publiclist',
    method:'post',
    data
  })
}

export function getThemeList(topictype,page) {//获取主题列表
  const data={
    topictype,
    page
  }
  return request({
    url:'/subject/topiclist',
    method:'post',
    data
  })
}
export function getEditTheme(topicid) {//编辑详情
  const data={
    topicid
  }
  return request({
    url:'/mytopic/detail',
    method:'post',
    data
  })
}
export function pushTheme(data) {//发布主题
  return request({
    url:'/mytopic/add',
    method:'post',
    data
  })
}
export function pushEditTheme(data) {//发布主题
  return request({
    url:'/mytopic/edit',
    method:'post',
    data
  })
}
export function delTheme(topicid) {//删除主题
  const data={
    topicid
  }
  return request({
    url:'/mytopic/del',
    method:'post',
    data
  })
}
export function delThemeFood(topicid,relationid) {//删除主题关联菜谱
  const data={
    topicid,
    relationid
  }
  return request({
    url:'/mytopic/itemdel',
    method:'post',
    data
  })
}




