<template>
  <li class="item" :class="size===1?'xs':''" @click="navToDetail(item.topicid)">
    <div class="cover br5 bg-img" :class="size===1?'lg':'xl'" :style="{ backgroundImage: 'url(' + osshost + item.topicimg +detailpic+ ')' }">
    </div>
    <div>
      <div class="name text-tips">{{ item.title }}</div>
      <div class="author">
        <img :src="osshost + item.faceimg + userface" class="headicon br50">
        <span class="nickname text-lips" style="max-width: 150px">{{ item.nickname }}</span>
        <span class="text-xm cuIcon-likefill margin-right-xs"></span>{{ item.collections }}
      </div>
    </div>
  </li>
</template>

<script>
  export default {
    name: "themeItem",
    data() {
    	return {
    		listpic:'?x-oss-process=style/list-img',
    		userface:'?x-oss-process=style/user-img',
    		detailpic:'?x-oss-process=style/detail-img',
        osshost: process.env.UPLOAD_URL,
    	};
    },
    props:{
      size:[String,Number],
      item:{
        type: Object,
        default: function(e) {
          return {};
        }
      }
    },
    methods: {
      navToDetail(topicid) {
        this.$router.push({path: "/themeDetail",query:{id:topicid}})
      }
    }
  }
</script>

<style scoped>

</style>



