import request from '@/utils/request'

export function followUser(fid) {//关注
  const data={
    fid
}
return request({
  url:'/myspace/follow',
  method:'post',
  data
})
}
export function unfollowUser(fid) {
  const data={
    fid
  }
  return request({
    url:'/myspace/unfollow',
    method:'post',
    data
  })
}

export function collect(itemid,itemtype) {//收藏
  const data={
    itemid,
    itemtype
  }
  return request({
    url:'/action/collection',
    method:'post',
    data
  })
}
export function unCollect(itemid,itemtype) {
  const data={
    itemid,
    itemtype
  }
  return request({
    url:'/action/uncollection',
    method:'post',
    data
  })
}
export function likeMenu(itemid,itemtype) {//点赞
  const data={
    itemid,
    itemtype
  }
  return request({
    url:'/action/like',
    method:'post',
    data
  })
}
export function unlikeMenu(itemid,itemtype) {
  const data={
    itemid,
    itemtype
  }
  return request({
    url:'/action/unlike',
    method:'post',
    data
  })
}


export function commentList(itemid,itemtype,page) {//获取评论列表
  const data={
    itemid,
    itemtype,
    page
  }
  return request({
    url:'/detail/replylist',
    method:'post',
    data
  })
}
export function pushReply(data) {//评论
  return request({
    url:'/action/reply',
    method:'post',
    data
  })
}



export function uploadImg(type) {//上传
  const data={
    type:type
  }
  return request({
    url:'/uploadphoto',
    method:'post',
    data
  })
}


